<template>
  <div>
    <div class="top">
      <h4>
    
        以下健康告知的询问对象是所有被保险人,请您根据您及连带被保人的健康状况,如实勾选。若未据实告知,保险人有权按照保险法的规定解除保险合同,对于合同解除前发生的保险事故,不承担赔偿或者给付保险金的责任。
      </h4>
      <p>
        1.被保险人是否从事油漆、有毒物质生产运输等职业?提示:职业或工种的变更
        本合同的被保险人变更其职业或工种时，您或被保险人应在10日内以书面形式通知我们。
        被保险人所变更的职业或工种,依照我们的职业分类属于拒保职业范围的,自职业或工种变更之日24时起,本合同终止,我们向您退还本合同的现金价值。自职业或工种变更之日24时之后发生保险事故的，我们不承担给付保险金的责任。
      </p>
      <p>
        2.被保险人过去1年内是否发现健康检查异常(如肿瘤标志物*、组织及细胞病理学检查异常、超声、X线、钼靶、CT、MRI、PET-CT、内镜等)?过去2年内是否住院或被要求进一步检查、手术或住院治疗?注:肿瘤标志物*:甲胎蛋白(AFP)、癌胚抗原(CEA).前列腺特异性抗原(PSA)、癌抗原125
        (CA125)癌抗原199(CA199)、癌抗原15-3(CA15-3)癌抗原50
        (CA50)、糖类抗原242(CA242)
        、胃癌相关抗原(CA72-4)、铁蛋白(SF).B2微球蛋白(B2-MG)、神经元特异性烯醇化酶(NSE)、鳞状细胞癌抗原(SCCA)、核基质蛋白-22
        (NMP-22),a-L-岩藻糖苷酶(AFU),人绒毛膜促性腺激素(HCG)。
      </p>
      <p>
        3.被保险人过去2年内投保人身保险或健康保险时,是否被保险公司拒保、延期、加费或者附加相关条件承保?
      </p>
      <div class="but">
        <div @click="partYes">部分为是</div>
        <div @click="allNo">以上均为否</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},

  data() {
    return {};
  },

  created() {},
  mounted() {},
  methods: {
    //   以上均为否
    allNo() {
      this.$router.push({
        path: "/insuredObject",
      });
    },
    // 部分为是
    partYes() {
      this.$router.push({
        path: "/insuranceInformation",
      });
    },
  },
  watch: {},
  computed: {},
};
</script>

